.deaths{
    display:block;
    margin: 20px auto 20px auto;;
    padding: 20px;
    border: 2px solid rgba(255, 0, 0, 0.089);
    background-color: rgba(255, 255, 255, 0.493);
    text-align: center;
    text-transform: uppercase;
    border-radius: 2%;
    width: 50vw;
    transition:0.3s;
}
.deaths:hover{
    background-color:rgba(255, 255, 255, 0.849) ;
}
.deaths h1{
    color: rgb(128, 0, 0);
    margin: 20px 0; 
}
.deaths p {
    color: red;
    font-size: 30px;
    margin: 15px 0;
}
.deaths span{
    font-weight: bold;
}
@media (max-width: 800px){
    .deaths{
        margin: 10px auto 10px auto;
        padding: 10px;
        width: 98vw;
    }
    .deaths h1{
        margin: 10px 0; 
        font-size: 18px;
       
    }
  }